.register__error {
    
    text-align: left;
    font-size: 12px;
    line-height: 15px;
    color: #FF0000;
    font-style: normal;
    font-weight: 400;
    position: absolute;
    top: 100%;
    margin-top: 2px;
    z-index: 200;
    visibility: visible;
    opacity: 1;
    transition: visibility 0.3s, opacity 0.3s linear;
  }